import React, { useState } from 'react'
import { initializeApp } from "firebase/app";
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import { getAuth, signInWithRedirect, GoogleAuthProvider, createUserWithEmailAndPassword, signInWithPopup, updateProfile, signInWithEmailAndPassword } from 'firebase/auth'
import { useAuthState } from "react-firebase-hooks/auth"
import "../css/signup.css"
import { useHistory } from 'react-router-dom'
import { firebaseConfig } from "../firebase"

const app = initializeApp(firebaseConfig);

const Signup = () => {
    const auth = getAuth()
    const [userisloggedin] = useAuthState(auth)
    const [name, setname] = useState("");
    const [password, setpassword] = useState("");
    const [loginname, setloginname] = useState("");
    const [loginpassword, setloginpassword] = useState("");
    const [error, seterror] = useState(false);
    const history = useHistory();


    const onLogin = () => {
        const auth = getAuth();
        signInWithEmailAndPassword(auth, loginname, loginpassword).then(() => {
            history.push('/')
            console.log("Logging in");
        }
        ).catch((err) => {
            console.log(err.message);
            seterror(true)
        }
        )

    }
    async function SignInWithGoogle() {
        const auth = getAuth();
        const provider = new GoogleAuthProvider();
        signInWithPopup(auth, provider).then((e) => {
            console.log(e.user.displayName);
            history.push('/')
        });
    }
    function signOut() {
        const auth = getAuth();
        auth.signOut();
    }
    // async function fetchdata() {
    //     const res = await fetch("https://api.unsplash.com/photos/?client_id=pvHLwntgGIrrErhByAuZLj0eZKDt7uyYDbe4Tk1ix44")
    //     const data = await res.json();
    //     console.log(data)
    // }
    // fetchdata()
    return (
        <>


        </>
    )

}
export default Signup
