import React, { useState } from 'react'
import { Button, TextField } from '@mui/material';
import { getAuth, signInWithRedirect, GoogleAuthProvider, createUserWithEmailAndPassword, signInWithPopup, updateProfile, signInWithEmailAndPassword } from 'firebase/auth'
import { useHistory } from 'react-router-dom'
import Error from './Alert';

const Register = () => {
    const auth = getAuth()
    // const [userisloggedin] = useAuthState(auth)
    const [name, setname] = useState("");
    const [password, setpassword] = useState("");
    const [loginname, setloginname] = useState("");
    const [loginpassword, setloginpassword] = useState("");
    const [error, seterror] = useState(false);
    const history = useHistory();
    const onRegister = async () => {
        const auth = getAuth();
        // createUserWithEmailAndPassword(auth, name, password).then(() => {
        //     updateProfile(auth.currentUser, { displayName: loginname })
        // }
        // ).catch(alert("Error")).then(alert("Account created"))
        // console.log("I am onRegister");
        try {
            await createUserWithEmailAndPassword(auth, name, password);
            updateProfile(auth.currentUser, { displayName: loginname });
            alert("Success!!Account created...You can now login with credentials")
        } catch (error) {
            alert(error.message);
        }
    }
    return (
        <>
            {error ? <Error /> : null}
            <div className="card">
                <form>
                    <div className="registerheading">
                        <h2 className="title">Register</h2>

                    </div>
                    <div className="social-login">
                        <button className="google-btn">
                            <img alt="Google"
                                src="data:image/svg+xml;base64," />
                            <p className="btn-text">Register with Google</p>
                        </button>
                    </div>

                    <p className="or"><span>or</span></p>

                    <div className="email-login">
                        <label htmlFor="email" className="my-2">Email</label>
                        <TextField autoComplete="off" type="email" id="standard-basic" variant="standard" value={name} onChange={(e) => setname(e.target.value)} />
                        {/* <input type="email" className="email input" required /> */}
                        <label htmlFor="password" style={{ marginTop: "12px" }} >Password</label>
                        {/* <input type="password" className="input password" required /> */}
                        <TextField type="password" id="standard-basic" variant="standard" style={{ marginTop: "2px" }} value={password} onChange={(e) => setpassword(e.target.value)} />
                    </div>
                    {/* <button className="submitbtn">Log In</button> */}
                    <Button className="my-3" variant="contained" color="error" style={{ borderRadius: "25px" }} onClick={onRegister}>Create Account</Button>
                </form>
            </div>

        </>
    )
}

export default Register;