import React, { useState } from 'react';
import { Button, TextField } from '@mui/material';
import { getAuth, signInWithRedirect, GoogleAuthProvider, createUserWithEmailAndPassword, signInWithPopup, updateProfile, signInWithEmailAndPassword } from 'firebase/auth'
import { Link, useHistory } from 'react-router-dom'

const Login = () => {
    const history = useHistory();
    // const [name, setname] = useState("");
    // const [password, setpassword] = useState("");
    const [loginemail, setloginemail] = useState("");
    const [loginpassword, setloginpassword] = useState("");
    const [error, seterror] = useState(false);

    const onLogin = async () => {
        const auth = getAuth();
        try {
            await signInWithEmailAndPassword(auth, loginemail, loginpassword);
            history.push('/')
            console.log("Logging in");
        } catch (error) {
            alert("Invalid credentials")
        }



    }
    return (
        <>
            <div className="card">
                <form>
                    <div className="registerheading">
                        <h2 className="title">Login</h2>
                        {/* <p className="subtitle">Dont have an account?
                            <Link to="/register" className="mx-1">Create</Link>
                        </p> */}

                    </div>
                    <div className="social-login">
                        <button className="google-btn">
                            <img alt="Google"
                                src="data:image/svg+xml;base64," />
                            <p className="btn-text">Register with Google</p>
                        </button>
                    </div>

                    <p className="or"><span>or</span></p>

                    <div className="email-login">
                        <div className="email-login">
                            <label htmlFor="email" className="my-2">Email</label>
                            <TextField autoComplete="off" type="email" id="standard-basic" variant="standard" value={loginemail} onChange={(e) => setloginemail(e.target.value)} />
                            {/* <input type="email" className="email input" required /> */}
                            <label htmlFor="password" style={{ marginTop: "12px" }} >Password</label>
                            {/* <input type="password" className="input password" required /> */}
                            <TextField type="password" id="standard-basic" variant="standard" style={{ marginTop: "2px" }} value={loginpassword} onChange={(e) => setloginpassword(e.target.value)} />
                        </div>
                    </div>
                    <Button className="my-3" variant="contained" color="error" style={{ borderRadius: "25px" }} onClick={onLogin}>Log In</Button>
                </form>
            </div>
            {/* <div className="container m-2">
                <input className="mx-1" type="text" value={loginemail} onChange={(e) => setloginemail(e.target.value)} />
                <input className="mx-1" type="password" value={loginpassword} onChange={(e) => setloginpassword(e.target.value)} />
                <button className="btn btn-success mx-1" onClick={onLogin}>Login</button>
            </div> */}

        </>
    )
}

export default Login
